<template>
    <Card class="h-100">
        <template #header>
            <Galleria :value="novel.covers" :numVisible="5"></Galleria>
        </template>
        <template #title>
            <div class="p-d-flex p-jc-between">
                <EditableLabel v-bind:value="novel.title" @update-label="updateTitle" placeHolderTitle="No title added yet."></EditableLabel>
                <div class="option-buttons">
                    <router-link :to="'/write/' + novel.id + '/chapters'">
                        <Button class="p-button-text" icon="pi pi-folder-open" iconPos="right"></Button>
                    </router-link>
                    <Button v-on:click="deleteNovel()" class="p-button-text p-button-danger" icon="pi pi-trash" iconPos="right" title="Delete"></Button>
                </div>  
            </div>        
        </template>
        <template #content>
            <EditableLabel v-bind:value="novel.summary" @update-label="updateSummary" ></EditableLabel>
        </template>
    </Card>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import EditableLabel from '../shared/inline-edit/EditableLabel.vue';

@Options({
    components: { EditableLabel }
})  
export default class Novel extends Vue {
  @Prop() novel!: Novel 

  updateTitle(title: string) {
    this.updateNovelProperty('title', title)
  }

  updateSummary(summary: string) {
    this.updateNovelProperty('summary', summary)
  }

  updateNovelProperty(key: string, value: any) {
    const novel = Object.assign({}, this.novel);
    novel[key] = value;
    this.$store.dispatch('updateNovel', novel) 
  }

  deleteNovel() {
    this.$confirm.require({
        message: 'Are you sure you want to proceed?',
        header: 'Confirmation',
        icon: 'pi pi-exclamation-triangle',
        accept: () => {
            this.$store.dispatch('deleteNovel', this.novel)
        }
    });
  }
}
</script>

<style scoped>
.option-buttons {
    width: 5em;
}
</style>