
import { Options, Vue } from "vue-class-component";
import { Emit } from "vue-property-decorator";

@Options({
    emits: ['start-edit', 'update', 'cancel']
})  
export default class InlineEdit extends Vue {
    editing = false;

    @Emit('start-edit')
    startEditMode() {
        this.editing = true;
    }

    @Emit('update')
    update($event) {
        $event.preventDefault();
        $event.stopPropagation();
        this.editing = false;
    }

    @Emit('cancel')
    cancel($event) {
        $event.preventDefault();
        $event.stopPropagation();
        this.editing = false;
    }
}

